.skills_section {
  background: #111;
}

.skills_banner {
  position: relative;
  top: -100px;
  width: 75%;
  margin: 0 auto;
  border-radius: 30px;
  background-color: #191919;
  padding: 2em;
}

.skills_banner h2 {
  text-align: center;
  color: #fff;
}

.skillsP {
  color: #fff;
  text-align: center;
  width: 90%;
  margin: 20px auto;
  font-weight: 500;
}

.skills_banner .nav-item {
  width: 33.33333%;
}

.skills_banner .nav-link {
  background-color: transparent;
  border-radius: 0;
  color: #fff;
  width: 100%;
  font-size: 17px;
  letter-spacing: 0.8px;
  font-weight: 500;
  position: relative;
  transition: 0.3s ease-in-out;
  text-align: center;
  z-index: 0;
}

.skills_banner .nav-link::before {
  content: "";
  position: absolute;
  width: 0;
  height: 100%;
  background: linear-gradient(90.21deg, #aa367c -5.91%, #4a2fbd 111.58%);
  top: 0;
  left: 0;
  z-index: -1;
  transition: 0.3s ease-in-out;
}

/* tab content */

.tab-content .col {
  text-align: center !important;
  color: #fff;
}

.tab-content .col svg {
  font-size: 2.4em;
}

.tab-content .col p {
  margin: 15px 0;
}
